.container {
  height: 88vh;
  display: flex;
  flex-direction: column;
  /* background-color: #e6beb6; */
  background-image: url(/src/images/background.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  color: white;
  justify-content: flex-end;
  margin: 1rem 5rem;
  border-radius: 30px;
}

.title {
  margin: 5rem;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3rem;
  font-weight: lighter;
}
