.recipe-container {
  height: 88vh;
  /* background-color: #e6beb6; */
  background-image: url(/src/images/background-add1.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  color: white;
  margin: 1rem 5rem;
  border-radius: 30px;
  display: flex;
  /* justify-content: center; */
  align-items: flex-end;
}

.add-recipe-title {
  /* text-align: center; */
  /* padding: 2rem; */
  width: 20%;
  margin: 5rem;
}

.recipe-container h2 span {
  font-weight: bold;
}

.recipe-container form {
  display: flex;
  flex-direction: column;
  width: 50%;
  align-self: baseline;
  /* padding: 2rem; */
  margin: 1rem 2rem 1rem 12rem;
}
.recipe-container p {
  margin: 0.5rem;
}

.recipe-container input,
.recipe-container textarea,
.recipe-container button {
  padding: 1rem;
  margin: 0.5rem;
  font-family: "Comfortaa", cursive;
  border-radius: 4px;
  color: #616060;
  border: none;
}

.recipe-container button {
  width: 30%;
  /* border: 2px solid #e68643; */
  text-align: center;
  background-color: #e68643;
  /* color: white; */
}

.recipe-container button:hover {
  transition: background-color 0.2s;
  background-color: transparent;
  border: 1px solid white;
}

.recipe-container .link {
  text-decoration: none;
  color: white;
}

.recipe-container .link:hover {
  color: white;
}
