*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Comfortaa", cursive;
}

/* NAVIGATION */

ul {
  display: flex;
  justify-content: center;
  font-family: "Raleway", sans-serif;
  margin: 1rem;
  position: sticky;
}

.nav-li {
  list-style-type: none;
}

.app-link {
  color: #c6c6c6;
  padding: 0 1rem;
  text-decoration: none;
}

.app-link:hover {
  color: #afaeae;
}
