.recipe-wrap {
  display: flex;
  /* justify-content: space-evenly; */
  min-height: 88vh;
  margin: 1rem 5rem;
}

.recipe-left {
  background-color: #e6beb6;
  border-radius: 30px 0 0 30px;
  color: white;
  width: 50%;
  position: relative;
}

.recipe-left button {
  background-color: transparent;
  border: none;
  color: white;
  font-family: "Raleway", sans-serif;
  position: absolute;
  top: 20px;
  left: 20px;
}

.recipe-left button:hover {
  text-decoration: underline;
}

.recipe-left h2 {
  padding: 4rem 6rem 2rem 6rem;
}

.recipe-left li {
  font-weight: lighter;
  font-family: "Raleway", sans-serif;
  letter-spacing: 0.1rem;
  margin-left: 1rem;
}

.img-ingred {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* margin: 1rem; */
  padding: 1rem;
}

.ingredients li {
  line-height: 1.3;
}

.recipe-right {
  width: 50%;
  color: #555555;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Raleway", sans-serif;
  letter-spacing: 0.1rem;
  padding: 1rem;
  position: relative;
}

.recipe-description {
  width: 80%;
}

.recipe-description li {
  list-style-type: none;
  margin: 0.5rem;
  font-size: 16px;
}

.recipe-description li span {
  color: #e6beb6;
  margin: 0.5rem;
}

.recipe-right p {
  position: absolute;
  bottom: 20px;
  right: 20px;
  font-size: 14px;
  color: #e6beb6;
}
