.collection-container {
  min-height: 110vh;
  color: white;
  margin: 1rem 5rem;
}

.collection-wrap {
  background-color: #e6beb6;
  border-radius: 30px 30px 0 0;
  height: 35vh;
}

.collection-wrap .recipes {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.collection-container h2 {
  padding: 4rem 6rem 2rem 6rem;
}

.collection-container p {
  color: black;
}

.recipe {
  width: 200px;
  height: 240px;
  border-radius: 4px;
  margin: 0.5rem;
}

.recipe-title {
  color: #555555;
  text-decoration: none;
  font-size: 13px;
}

.recipe-title:hover {
  color: black;
}
